.headerNavbar {
  border-bottom: 2px solid lightgray;
  position: sticky !important;
  height: 75px;
  display: flex;
  align-items: center;
  background-color: lightgray;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  font-family: "Baloo Da 2", cursive;
  z-index: 999;
}
.navbar-brand {
  margin-right: 3rem !important;
}

.header__category {
  margin-left: -47px;
}
.header__category .inputCategory {
  border: 2px solid black;
  padding: 12px;
  padding-left: 50px;
  margin-top: 27px;
  border-radius: 5px;
  width: 170px;
}

.searchIcon {
  color: #3b8138;
  position: relative;
  left: 38px;
  top: 3px;
  font-size: 25px;
  opacity: 0.7;
}

.header__caretDown {
  color: #3b8138;
  opacity: 0.7;
  font-size: 32px;
  position: relative;
  right: 39px;
  bottom: -3px;
}
.ContainerFluid {
  padding: 0 !important;
  margin: 0 !important;
}

.logoParent {
  margin-top: -27px !important;
}

.header__search {
  margin-top: -18px !important;
}
.header__search .inputSearch {
  width: 90%;
  border: 2px solid black;
  padding: 12px;
  border-radius: 5px;
}
.header__searchParent {
  position: relative;
  top: 8px;
}
.header__searchBtn {
  background-color: transparent;
  color: #3b8138;
  font-size: 50px;
  padding: 12px;
}
.header__searchBtn:hover {
  background-color: #3b8138;
  color: #ffffff;
  cursor: pointer;
}
.loginParent {
  margin-left: 91px;
  margin-top: 12px;
}
.header__login {
  color: black;
  text-decoration: none;
  font-size: 20px;
  font-weight: 600;
  cursor: pointer;
  margin-right: 20px;
  margin-top: 12px;
}

.header__login:hover {
  color: #3b8138;
}

.header__sell {
  color: black;
  font-size: 20px;
  font-weight: 400;
  border-radius: 50px;
  margin-left: 20px;
  background-color: lightgray;
  padding-left: 26px;
  padding-top: 9px;
  position: relative;
  display: inline-block;
  cursor: pointer;
  width: 104px;
  height: 48px;
}

.rowParent div {
  padding: 0 !important;
}

@media (max-width: 1100px) {
  .header__search .inputSearch {
    width: 80% !important;
  }
  .header__caretDown {
    display: none;
  }
  .rowParent {
    display: flex;
    flex-wrap: nowrap !important;
  }
}

@media (max-width: 1029px) {
  .header__category,
  .header__searchParent {
    display: none;
  }
}
